let string = `.skin* {
    box-sizing: border-box;
    margin: 0;
    padding: 0
}

.skin::after,
::before {
    box-sizing: border-box;
}

.skin {
    position: relative;
    min-height: 100vh;
    width: 100%;
    background: rgb(254, 228, 51);
}

.wrapper {
    position: fixed;
    /* border: 1px solid red; */
    width: 300px;
    height: 500px;
    top: 300px;
    left: 50%;
    margin-left: -150px;
}

.nose {
    /*做扇形*/
    position: absolute;
    left: 50%;
    top: 30px;
    margin-left: -10px;
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-top-color: #000;
    border-radius: 10px;
}

.eye {
    position: absolute;
    border: 2px solid black;
    width: 55px;
    height: 55px;
    left: 50%;
    top: 0px;
    margin-left: -28px;
    border-radius: 50%;
    background-color: #2e2e2e;
}

.eye::before {
    display: block;
    content: '';
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: white;
    border: 2px solid #000;
    margin-left: 5px;
    margin-top: 2px;
}

.eye.left {
    transform: translate(-90px);
}

.eye.right {
    transform: translate(90px);
}

.face {
    border: 3px solid black;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: red;
    position: absolute;
    top: 100px;
    left: 50%;
}

.face.left {
    margin-left: 80px;
}

.face.right {
    margin-left: -170px;
}

.mouth {
    /* border: 1px solid green; */
    width: 200px;
    height: 200px;
    position: absolute;
    left: 50%;
    top: 75px;
    margin-left: -100px;
}

.mouth::before {
    content: '';
    display: block;
    border: 3px solid black;
    background-color: rgb(254, 228, 51);
    border-top: transparent;
    border-right: transparent;
    border-radius: 0 0 0 25px;
    transform: rotate(-20deg);
    width: 90px;
    height: 25px;
    position: absolute;
    top: -10px;
    left: 50%;
    margin-left: -91px;
    z-index: 1;
}

.mouth::after {
    content: '';
    display: block;
    border: 3px solid black;
    background-color: rgb(254, 228, 51);
    border-top: transparent;
    border-left: transparent;
    border-radius: 0 0 25px 0;
    transform: rotate(20deg);
    width: 90px;
    height: 25px;
    position: absolute;
    top: -10px;
    left: 50%;
    z-index: 1;
}

.mouth1 {
    border: 2px solid black;
    background-color: #cc4143;
    position: absolute;
    width: 130px;
    height: 140px;
    left: 50%;
    margin-left: -68px;
    top: -3px;
    border-bottom-left-radius: 200px 400px;
    border-bottom-right-radius: 200px 400px;
    overflow: hidden;
}

.mouth2 {
    /* border: 2px solid black; */
    background-color: #dd666a;
    position: absolute;
    width: 180px;
    height: 150px;
    left: 50%;
    margin-left: -90px;
    top: 20px;
    border-top-left-radius: 105px 170px;
    border-top-right-radius: 105px 170px;
    z-index: 1;
}`
let n = 1
demo.innerText = string.substring(0, n) //写文本
demo1.innerHTML = string.substring(0, n) //写CSS

let time = 100
const run = () => {
    n += 1
    if (n > string.length) {
        window.clearInterval(id)
        return
    }
    demo.innerText = string.substring(0, n)
    demo1.innerHTML = string.substring(0, n)
    demo.scrollTop = demo.scrollHeight
}

const play = () => { return setInterval(run, time) } //setInterval第一个参数不加括号

const pause = () => { window.clearInterval(id) }

let id = play()

Pause.onclick = () => {
    pause()
}
Play.onclick = () => {
    id = play()
}
Slow.onclick = () => {
    pause()
    time = 300
    id = play()
}
Normal.onclick = () => {
    pause()
    time = 100
    id = play()
}
Fast.onclick = () => {
    pause()
    time = 0
    id = play()
}